        name: "",
      },
      // 
      orderSuccessRules: {
        staff_number: [
          { required: true, message: "", trigger: "blur" },
        ],
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      // id
      currentOrderId: "",

      // 
      interviewFormData: [],
      interviewLoading: false,

      // 
      addInterviewDialogVisible: false,
      interviewForm: {
        customer_id: "",
        staff_id: "",
        content: "",
        status: "",
        interview_time: "",
        name: "",
      },
      editInterviewState: {
        id: "",
        content: "",
        status: "",
      },
      editInterviewDialogVisible: false,
      interviewFormRules: {
        status: [
          { required: true, message: "", trigger: "blur" },
        ],
